}

html {
  height: 100%;
}

h1{
  margin-bottom: 0;
  color:#FE4A4A;
}
.topnav{
  padding-left: 1rem;
  width: 1000px;
  height: 20px;

}



body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* overflow: hidden; */
}

html,
body,
#root {
  display: flex;
  flex-direction: column;
  flex: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button,
input,
